
import React, { Component } from 'react'
import store from '../store.js'
import { Provider,connect } from 'react-redux'


import bg3 from '../asset/img/bg3.webp'

class ResultScene extends Component {
    constructor(props) {
        super()
        this.state = {
            resultList:[
              {
                id:0,
                name:'',
                imgUrl:require('../asset/img/tu2.webp'),
                keyWords:['',''],
                describe: ''

              },
              {
                id: 1,
                name: '',
                imgUrl: require('../asset/img/tu3.webp'),
                keyWords: ['', ''],
                describe: ''
              },
              {
                id: 2,
                name: '',
                imgUrl: require('../asset/img/tu3.webp'),
                keyWords: ['', ''],
                describe: ''
              },
              {
                id: 3,
                name: '',
                imgUrl: require('../asset/img/tu3.webp'),
                keyWords: ['', ''],
                describe: ''
              },
              {
                id: 4,
                name: '',
                imgUrl: require('../asset/img/tu4.webp'),
                keyWords: ['', ''],
                describe: ''
              },
              {
                id: 5,
                name: '',
                imgUrl: require('../asset/img/tu5.webp'),
                keyWords: ['', ''],
                describe: ''
              }
            ]
        }
        this.nextPage = this.nextPage.bind(this)
        this.zdyc = this.zdyc.bind(this)
        this.gywm = this.gywm.bind(this)
    }
  render() {
    var index = 0
    switch(this.props.score){
      case 0:
        index = 0;
        break;
      case 5:
        index = 1;
        break;
      case 10:
        index = 2;
        break;
      case 15:
        index = 3;
        break;
      case 20:
        index = 4;
        break;
      case 25:
        index = 5;
        break;
      default:
        index = 5
    }
    var result = this.state.resultList[index]
    return (
      <div className='ResultScene'>
        <h2></h2>
        
          <h3>{result.name}</h3>
          <h4>{result.keyWords.map((item,index)=>{ return <span key={index}>{item}</span>       })}</h4>
          <img src={result.imgUrl} />              
          <p>{result.describe}</p>

        <button className='zdyc' onClick={this.zdyc}></button>
        <button className='gywm' onClick={this.gywm}></button>
      </div>
    )
  }
  componentDidMount() {
      
  }
  nextPage(index){
    // console.log(this.props.mySwiper)

    var score = this.props.content.answer[index].score
    console.log(score)
    this.props.onAddScore(score)

    this.props.mySwiper.slideNext()
  }


  zdyc(index){
    this.props.mySwiper.slideNext()
  }
  gywm(){
    this.props.mySwiper.slideNext()
  }
}



function mapStateToProps(state) {
  return {
    score: state.score,
    
  }
}

//props


function mapDispatchToProps(dispatch) {
  return {
    // onIncreaseClick: () => dispatch(increaseAction)
    onAddScore: function (score) {
      dispatch({
        type: 'addScore',
        currentScore:score
      })
    },
    
  }
}

//connectCounterpropsstatestate

ResultScene = connect(
  mapStateToProps,
  mapDispatchToProps
)(ResultScene)


export default ResultScene


